@use 'variaveis' as v;
@use 'textos' as t;

$logoUmTamanho: 35px;
$logoDoisTamanho: 28px;

.hero{
    padding: 2vw 5.6vw;
    min-height: 80vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    background-image: url(../../Imagens_projeto_4/House_gumball.jpg);
    background-size: cover;
    background-repeat: no-repeat;

    &__image{
        max-height:42vw;
        top: 0;
    }

    &__content{
        max-width: 700px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .titulo--pequeno{
            margin-bottom: 40px;
            text-align: center;
        }
    }

    &__logo{
        text-align: center;
        img{
            max-width: 320px;
            margin: 0 auto 38px;
        }
    }

    &__streamings{
        display: flex;
        justify-content: space-around;
    }

    &__streaming{
        width: calc(45% - 6px);
        text-align: center;

        &__logo{
            max-height: 50px; 
        }
    }
}

@media screen and (min-width:769px) and (max-width: 1023px) {
    .hero{
        background-image: url(../images/hero_tablet.jpg);
        padding-top: calc(50vw + 35px);
        background-size: contain;
        background-repeat: no-repeat;
        margin-top: -80px;

        &__conteudo{
            margin: 0 auto;
        }
        
        &__logo{
            max-width: 280px;
            margin: 0 auto 38px;
        }
    }
}

